/** @jsx jsx */
import { jsx, keyframes } from '@emotion/core';
import { Box, PseudoBox } from '@chakra-ui/core';

function createKeyframes(numberOfElements) {
  return keyframes`
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * ${numberOfElements}))}
`;
}

export default function BackgroundSlider({ children, numberOfElements, duration }) {
  return (
    <Box overflow="hidden" position="relative" w="100%">
      <Box
        display="flex"
        css={{ animation: `${createKeyframes(numberOfElements)} ${duration}s linear infinite` }}
        w={250 * numberOfElements * 2}
      >
        {children}
        {children}
      </Box>
    </Box>
  );
}

export function SliderItem({ children }) {
  return (
    <PseudoBox
      display="flex"
      alignItems="center"
      justifyContent="center"
      minWidth="250px"
      w="250px"
      transition="all 0.2s ease"
      css={{ filter: 'grayscale(1)' }}
      opacity="0.75"
      _hover={{ filter: 'none', opacity: 1, transform: 'scale(1.05)' }}
    >
      {children}
    </PseudoBox>
  );
}
